g.dimension {
  cursor: pointer;
}
g.dimension circle {
    -webkit-transition: stroke .3s, fill .3s, -webkit-transform .3s;
    transition: stroke .3s, fill .3s, -webkit-transform .3s;
    transition: stroke .3s, fill .3s, transform .3s;
    transition: stroke .3s, fill .3s, transform .3s, -webkit-transform .3s;
}
g.dimension circle.dimension-normal {
    stroke: #555;
    stroke-width: 2px;
    fill: none;
    -webkit-transition: r 0.3s;
    transition: r 0.3s;
}
g.dimension circle.inner {
    stroke: none;
    r: 4px !important;
    fill: #555;
}
g.dimension text.dimension {
    -webkit-transition: fill .3s, font-size .3s, -webkit-transform .2s;
    transition: fill .3s, font-size .3s, -webkit-transform .2s;
    transition: fill .3s, transform .2s, font-size .3s;
    transition: fill .3s, transform .2s, font-size .3s, -webkit-transform .2s;
    text-anchor: middle;
}
g.dimension.selection text {
    fill: #C2185B;
}
g.dimension.selection circle.dimension-normal {
    fill: none;
    stroke: #C2185B;
}
g.dimension.selection circle.inner {
    fill: #C2185B;
    stroke: none;
    r: 6px;
}
g.dimension circle.colorDimension {
    r: 15px;
    fill: none;
    stroke-width: 2px;
}
g.dimension.disable circle {
  stroke: #ccc !important;
  stroke-width: 1px !important;
}
g.dimension.disable circle.inner {
  fill: none !important;
}
g.dimension.disable text {
  fill: #ccc !important;
}
